<template>
  <div class="new-article">
    <h3>New Article</h3>
    <div class="row">
      <form @submit.prevent="saveArticel" class="cal s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="article_id" required>
            <label>Article ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="title" required>
            <label>Title</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="body" required>
            <label>Body</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to ="/" class="btn grey">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit';

  export default {
    name: 'new-article',
    data() {
      return {
        article_id: null,
        title: null,
        body: null
      }
    },
    methods: {
      saveArticel() {
        db.collection('articles')
          .add({
            article_id: this.article_id,
            title: this.title,
            body: this.body
          })
          .then(docRef => this.$router.push('/'));
      }
    }
  }
</script>



