<template>
  <div class="edit-article">
    <h3>Edit Article</h3>
    <div class="row">
      <form @submit.prevent="updateArticel" class="cal s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" disabled v-model="article_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="title" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="body" required>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to ="/" class="btn grey">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit';

  export default {
    name: 'edit-article',
    data() {
      return {
        article_id: null,
        title: null,
        body: null
      }
    },
    beforeRouteEnter(to, from ,next) {
      db.collection('articles')
        .where('article_id', '==', to.params.article_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.article_id = doc.data().article_id,
              vm.title = doc.data().title,
              vm.body = doc.data().body
            });
          });
        });
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('articles')
          .where('article_id', '==', this.$route.params.article_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.article_id = doc.data().article_id,
              this.title = doc.data().title,
              this.body = doc.data().body
            });
          });
      },
      updateArticel() {
        db.collection('articles')
          .where('article_id', '==', this.$route.params.article_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                article_id: this.article_id,
                title: this.title,
                body: this.body
              })
              .then(_ => {
                this.$router.push({name: 'view-article', params: {article_id: this.article_id}})
              });
            });
          });
      }
    }
  }
</script>



