<template>
  <div class="view-article">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{article_id}} {{title}}</h4>
      </li>
      <li class="collection-item">{{body}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteArticle" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-article', params: {article_id: article_id}}"
        class="btn-floating btn-large red">
        <i class="fa fa-pen"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit';

  export default {
    name: 'view-article',
    data() {
      return {
        article_id: null,
        title: null,
        body: null
      }
    },
    beforeRouteEnter(to, from ,next) {
      db.collection('articles')
        .where('article_id', '==', to.params.article_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.article_id = doc.data().article_id,
              vm.title = doc.data().title,
              vm.body = doc.data().body
            });
          });
        });
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('articles')
          .where('article_id', '==', this.$route.params.article_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.article_id = doc.data().article_id,
              this.title = doc.data().title,
              this.body = doc.data().body
            });
          });
      },
      deleteArticle() {
        if (confirm('Are you sure?')) {
        db.collection('articles')
          .where('article_id', '==', this.$route.params.article_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push('/');
            });
          });
        }
      }
    }
  }
</script>



